@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App-logo{height:6vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.title{margin:0;padding:0}.App-header{background-color:#282c34;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;color:white}.App-link{color:#61dafb}.btn{-webkit-align-items:flex-end;align-items:flex-end}.mode-icon{height:6vmin;width:6vmin;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;margin-bottom:20px}.container{max-width:500px;margin:30px auto;overflow:auto;min-height:300px;border:1px solid steelblue;padding:30px;border-radius:5px}.btn{display:inline-block;background:#000;color:#fff;border:none;padding:10px 20px;margin:5px;border-radius:5px;cursor:pointer;text-decoration:none;font-size:15px;font-family:inherit}.btn:focus{outline:none}.btn:active{-webkit-transform:scale(0.95);transform:scale(0.95)}.btn-block{display:block;width:100%}.task{background:#f4f4f4;margin:5px;padding:10px 20px;cursor:pointer}.task.reminder{border-left:5px solid green}.task h3{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.add-form{margin-bottom:40px}.form-control{margin:20px 0}.form-control label{display:block}.form-control input{width:100%;height:40px;margin:5px;padding:3px 7px;font-size:17px}.form-control-check{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.form-control-check label{-webkit-flex:1 1;flex:1 1}.form-control-check input{-webkit-flex:2 1;flex:2 1;height:20px}

